.emplacement {
  display: flex;
  justify-content: center;
  align-items: center;


}

.profil-page {
  margin: 200px auto 0 auto;
  width: 600px;

  .logo1 {
    position: relative;
    bottom: 90px;
    display: flex;
    justify-content: center;
  }

  .icons {
    top: 55px;
  }

  .log-container {
    min-width: 60%;
    margin: 0 auto;
    padding: 0;


    @media screen and(max-width: 885px) {}
  }
  #connection-form_logIn {
    padding: 160px 40px 40px 40px; 
  }
  .connection-form {
    border: none;
    /* background: linear-gradient(180deg, rgba(169,191,115,1 0.58%, rgba(0,0,0,1) 100%); */
    background: url('../../../public/img/icons/Alertwindows.png') no-repeat;
    background-size: cover;
    min-width: 600px;
    height: auto;
    text-align: center;
    margin: 0 auto;
    top: -80px;
    padding: 160px 40px 40px 40px; 
  


    @media screen and(max-width: 550px) {
      min-width: 180px;

    }

    .form-container {
      height: auto;
      display: grid;
      grid-template-columns: 30% 70%;
      margin: 0;
      padding: 0;

      @media screen and(max-width: 550px) {
        grid-template-columns: 1fr;
      }

      ul {
        li {
          cursor: pointer;
          border-radius: 20px;
          padding: 10px 20px;
          transition: 0.2s;

          @media screen and(max-width: 550px) {
            width: 80%;
            margin: 0 auto;
          }
        }

        .active-btn {
          background: $color-1;
          margin: 5px;

          &:hover {
            background: $color-2;
          }
        }

        li:not(.active-btn) {
          &:hover {
            background: $color-2;
            transform: translateX(3px);
          }
        }

        #login {
          position: relative;

          @media screen and(max-width: 550px) {
            margin-bottom: 30px;

            &:after {
              content: "";
              position: absolute;
              bottom: -10px;
              left: 50%;
              transform: translateX(-50%);
              width: 80%;
              height: 1px;
              background: $color-2;
            }
          }
        }
      }

      form {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        height: auto;
        margin: 0;
        padding: 0;

        .conditions-generales {
          display: flex;
          align-items: center;
        }

        input {
          width: 80%;
          padding: 10px 25px 10px 60px;
          margin: 10px 0;
          font-size: 1.2rem;
          font-family: $font-2;
          background-color: $color-1;
          color: #EBFFC2;

          &:focus {
            background-color: #a8c75b;
          }

        }

        input[type="checkbox"] {
          appearance: none;
          background: none;
          padding: 0;
          border-radius: 6px;
          border: 1px solid rgba(112, 112, 112, 1);
          width: 30px;
          height: 30px;
          margin-right: 10px;
          &:checked {
            background: url('../../../public/img/icons/fermer.png') no-repeat;
            background-size: 50px;
            background-position: center;

          }
        }

        #pseudo {

          background-image: url('../../../public/img/icons/compte.png');
          background-repeat: no-repeat;
          background-position: 5px;
          background-size: 50px;

          border-top: 2px solid #EBFFC2;
          border-bottom: 1px solid #939273;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
          border-bottom-left-radius: 15px;
          border-bottom-right-radius: 15px;

          &::placeholder {
            text-align: center;
            color: #f6ffe6;


          }
        }

        #email {
          background-image: url('../../../public/img/icons/email.png');
          background-repeat: no-repeat;
          background-position: 5px;
          background-size: 50px;

          border-top: 2px solid #EBFFC2;
          border-bottom: 1px solid #939273;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
          border-bottom-left-radius: 15px;
          border-bottom-right-radius: 15px;

          &::placeholder {
            text-align: center;
            color: #f6ffe6;

          }
        }

        #password {
          background-image: url('../../../public/img/icons/PayeSecu.png');
          background-repeat: no-repeat;
          background-position: 5px;
          background-size: 50px;

          border-top: 2px solid #EBFFC2;
          border-bottom: 1px solid #939273;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
          border-bottom-left-radius: 15px;
          border-bottom-right-radius: 15px;

          &::placeholder {
            text-align: center;
            color: #f6faee;

          }
        }

        #password-conf {
          background-image: url('../../../public/img/icons/PayeSecu.png');
          background-repeat: no-repeat;
          background-position: 5px;
          background-size: 50px;

          border-top: 2px solid #EBFFC2;
          border-bottom: 1px solid #939273;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
          border-bottom-left-radius: 15px;
          border-bottom-right-radius: 15px;

          &::placeholder {
            text-align: center;
            color: #f6ffe6;
          }
        }

        a {
          color: #80af09;
        }

        #soumettre {
          color: black;
          font-family: $font-1;
          letter-spacing: 3px;
          padding: 14px 20px;

          &:hover {
            background: $color-2;
          }
        }
      }
    }
  }

  h1 {
    text-align: center;
    margin: 10px 0 6px;
  }

  .img-container {
    display: grid;
    align-items: center;
    height: 100%;

    img {
      height: 280px;

      @media screen and(max-width: 1050px) {
        height: 220px;
      }

      @media screen and(max-width: 950px) {
        height: 200px;
      }

      @media screen and(max-width: 885px) {
        display: none;
      }
    }
  }

  .update-container {
    display: grid;
    grid-template-columns: 50% 50%;

    @media screen and(max-width: 700px) {
      grid-template-columns: 1fr;
    }

    >div {
      background: $color-4;
      border-radius: 20px;
      padding: 25px;
      margin: 20px 10px;

      @media screen and(max-width: 700px) {
        margin: 10px 10px;
      }
    }

    .left-part {
      text-align: center;

      h3 {
        margin-bottom: 12px;
      }

      img {
        height: 180px;
        width: 180px;
        border-radius: 46px;
        object-fit: cover;
        margin-bottom: 30px;
        box-shadow: 0 0 6px rgba(51, 51, 51, 0.363);
      }

      form {
        position: relative;

        label {
          position: absolute;
          transform: translate(-50%, -50%);
          width: 20px;
          background: $color-3;
          color: $color-2;
          border-radius: 20px;
          display: inline-block;
          padding: 9px 0;
          transition: 0.2s;
          z-index: 10;
          cursor: pointer;
        }

        input[type="file"] {
          position: absolute;
          opacity: 1;
          transform: translate(-3px, -50%);
          cursor: pointer;
          padding: 10px 20px;

          @media screen and(max-width: 1121px) {
            opacity: 0;
          }
        }

        input[type="submit"] {
          margin-top: 10px;
          border-radius: 20px;

          &:hover {
            letter-spacing: 1px;
          }

        }
      }

      p {
        color: red;
        font-family: $font-3;
        margin-top: 6px;
      }
    }

    .right-part {
      text-align: center;
      padding: 20px 40px;

      h3 {
        margin-bottom: 12px;
      }

      p,
      textarea {
        min-height: 80px;
        border: 1px solid $color-1;
        border-radius: 20px 20px 6px 20px;
        padding: 10px 20px;
        font-size: 1.1rem;
      }

      textarea {
        width: 100%;
        margin-bottom: -6px;
        font-size: 1.2rem;
      }

      button {
        margin: 5px 0;
        float: right;
      }

      h4 {
        margin: 60px 0 10px;
      }

      h5 {
        background: $color-1;
        color: $color-4;
        font-family: $font-1;
        margin: 20px 0;
        padding: 12px;
        font-size: 1.1rem;
        border-radius: 20px;
        transition: 0.2s;
        cursor: pointer;

        &:hover {
          filter: brightness(110%);
          transform: translateX(2px);
        }
      }
    }
  }

  .popup-profil-container {
    z-index: 100;
    width: 100%;
    top: 0;
    left: 0;
    height: 100%;
    position: fixed;
    animation: popup 1s forwards;

    @keyframes popup {
      to {
        backdrop-filter: blur(2px);
      }
    }

    .modal {
      position: absolute;
      top: 10%;
      left: 50%;
      transform: translate(-100%);
      background: $color-2;
      padding: 25px;
      border-radius: 20px;
      box-shadow: 0 0 2px rgba(131, 130, 130, 0.356);
      overflow: auto;
      transform: scaleY(0);
      transform-origin: center;
      animation: modal .5s forwards;

      h3 {
        min-width: 200px;
      }

      @keyframes modal {
        to {
          transform: scale(1) translate(-50%);
        }
      }

      .cross {
        position: absolute;
        top: 25px;
        right: 25px;
        transition: 0.1s;
        cursor: pointer;

        &:hover {
          transform: scale(1.07);
        }
      }

      ul {
        margin: 20px 0 0;
        max-height: 500px;
        overflow-y: scroll;
        overflow-x: hidden;

        &::-webkit-scrollbar {
          width: 2px;
        }

        &::-webkit-scrollbar-track {
          background: $color-4;
        }

        &::-webkit-scrollbar-thumb {
          background: $color-1;
        }

        &::-webkit-scrollbar-thumb:hover {
          background: #555;
        }

        li {
          display: grid;
          grid-template-columns: 64px 1fr 114px;
          align-items: center;
          margin: 10px 0;

          img {
            height: 50px;
            width: 50px;
            border-radius: 20px;
            object-fit: cover;
            box-shadow: 0 0 2px rgba(51, 51, 51, 0.376);
          }

          h4 {
            text-align: left;
            min-width: 210px;
          }

          .follow-handler {
            text-align: left;

            button {
              background: $color-4;
              border-radius: 20px;
              margin-right: 6px;

              &:hover {
                color: $color-3;
                transform: translateX(2px);
              }
            }
          }
        }
      }
    }
  }
}